import React, { Component } from 'react';
import '../assets/style/PostEdit.css';

// import components
import Button from './Buttons';

class Postedit extends Component {
  constructor(props) {
    super(props);
    const postId = this.props.match.params.postId
      ? this.props.match.params.postId
      : '';
    this.state = {
      postId: postId,
      title: '',
      author: '',
      content: '',
      label: '',
      topped: false,
      recommend: false
    };
    this.handleInputChange = this.handleInputChange.bind(this);
  }

  /**
   * 
   * @param event
   */
  handleInputChange(event) {
    const target = event.target;
    const value = target.type === 'checkbox' ? target.checked : target.value;
    const name = target.name;
    this.setState({
      [name]: value
    });
  }
  componentWillReceiveProps(nextProps) {
    console.log(nextProps);
  }
  render() {
    return (
      <div className="postedit-container main-view">
        <div className="postedit-edit-card main-view-panel">
          <h1 className="postedit-title main-view-title"></h1>
          <form
            className="postedit-form"
            onSubmit={e => {
              const url = this.state.postId
                ? `/updatepost/${this.state.postId}`
                : `/admin/addpost`;
              const post = {};
              post.title = this.state.title;
              post.author = this.state.author;
              post.content = this.state.content;
              post.label = this.state.label;
              post.topped = this.state.topped;
              post.recommend = this.state.recommend;
              const postParam = {
                url: url,
                data: post,
                requestType: 'ADD_POSTS'
              };
              this.props.handleSubmit(postParam);
              e.preventDefault();
            }}
          >
            <div className="postedit-form-body">
              <label className="form-label">
                <span className="form-input-label">:</span>
                <input
                  type="text"
                  name="title"
                  className="form-control form-input"
                  placeholder=""
                  value={this.state.title}
                  onChange={this.handleInputChange}
                />
              </label>
              <label className="form-label">
                <span className="form-input-label">:</span>
                <input
                  name="author"
                  type="text"
                  className="form-control form-input"
                  placeholder=""
                  onChange={this.handleInputChange}
                  value={this.state.author}
                />
              </label>
              <label className="form-label">
                <span className="form-textarea-label">:</span>
                <textarea
                  name="content"
                  className="form-control form-textarea"
                  onChange={this.handleInputChange}
                  value={this.state.content}
                />
              </label>
              <label className="form-label">
                <span className="form-input-label">:</span>
                <select
                  name="label"
                  className="form-control form-select"
                  onChange={this.handleInputChange}
                  value={this.state.label}
                >
                  <option value="javascript">javascript</option>
                  <option value=""></option>
                  <option value="nodejs">nodejs</option>
                  <option value="Python">Python</option>
                  <option value=""></option>
                </select>
              </label>
              <label className="form-label">
                <span className="form-input-label">:</span>
                <input
                  name="topped"
                  type="checkbox"
                  className="form-control form-radio"
                  onChange={this.handleInputChange}
                  checked={this.state.topped}
                />
              </label>
              <label className="from-label">
                <span className="form-input-label">:</span>
                <input
                  name="recommend"
                  type="checkbox"
                  className="form-control form-radio"
                  onChange={this.handleInputChange}
                  checked={this.state.recommend}
                />
              </label>
              <div className="postedit-button-rows">
                <Button
                  buttonClass="buttons-primary"
                  buttonType="submit"
                  text=""
                />
              </div>
            </div>
          </form>
        </div>
      </div>
    );
  }
}

export default Postedit;



